import React from 'react';
import { 
  Routes, 
  Route, 
  Navigate, 
  Outlet 
} from 'react-router-dom';
import { BrowserRouter as Router } from 'react-router-dom';
import { ChakraProvider } from '@chakra-ui/react';
import theme from './theme';

// Enable future flags globally (you can customize as needed)
const futureConfig = {
  v7_startTransition: true,
  v7_relativeSplatPath: true,
};

// Protected Route component
const ProtectedRoute = () => {
  return <Outlet />;  // Temporary simplification
};

function App() {
  return (
    <ChakraProvider theme={theme}>
      <Router future={futureConfig}>  // Add future config here
        <Routes>
          {/* Public Routes */}
          <Route path="/" element={<div>Landing Page Placeholder</div>} />
          <Route path="/register" element={<div>Register Page Placeholder</div>} />
          <Route path="/login" element={<div>Login Page Placeholder</div>} />
          <Route path="/about" element={<div>About Page Placeholder</div>} />

          {/* Add OAuth callback route - using placeholder */}
          <Route path="/auth/callback" element={<div>OAuth Callback Placeholder</div>} />

          {/* Protected Routes */}
          <Route element={<ProtectedRoute />}>
            <Route path="/home" element={<div>Home Page Placeholder</div>} />
            <Route path="/dashboard" element={<div>Dashboard Page Placeholder</div>} />
            <Route path="/profile" element={<div>Profile Page Placeholder</div>} />
            <Route path="/top-up" element={<div>Top Up Page Placeholder</div>} />
            <Route path="/settings" element={<div>Settings Page Placeholder</div>} />
            <Route path="/impact" element={<div>Impact Page Placeholder</div>} />
            <Route path="/expenses" element={<div>Expenses Page Placeholder</div>} />
            <Route path="/notifications" element={<div>Notifications Page Placeholder</div>} />
            <Route path="/support" element={<div>Support Page Placeholder</div>} />
            <Route path="/forum" element={<div>Forum Page Placeholder</div>} />
            <Route path="/loadshedding" element={<div>Load Shedding Page Placeholder</div>} />
            <Route path="/refer" element={<div>Refer Page Placeholder</div>} />
            <Route path="/group-buying" element={<div>Group Buying Placeholder</div>} />
            <Route path="/subscription" element={<div>Subscription Page Placeholder</div>} />
          </Route>

          {/* Fallback route */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </Router>
    </ChakraProvider>
  );
}

export default App;
